import React, { Component } from "react";
import dynamic from "next/dynamic";

const HelloBundle = dynamic({
  modules: () => {
    const components = {
      Hello1: () => import("../components/dynamic/hello1"),
      Hello2: () => import("../components/dynamic/hello2")
    };

    return components;
  },
  render: (props, { Hello1, Hello2 }) => (
    <div>
      <h1>{props.title}</h1>
      <Hello1 />
      <Hello2 />
    </div>
  )
});

class DynamicBundle extends Component {
  render() {
    return <HelloBundle title="Dynamic Bundle" />;
  }
}

export default DynamicBundle;
